import { Component, OnInit, Injector } from '@angular/core';
import { FormGroup, Validators, FormBuilder, FormArray } from '@angular/forms';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

@Component({
	selector: 'app-income-tracker-setup-penalty-setup',
	templateUrl: './income-tracker-setup-penalty-setup.component.html',
	styleUrls: ['./income-tracker-setup-penalty-setup.component.scss']
})
export class IncomeTrackerSetupPenaltySetupComponent implements OnInit {

	unitFieldType: string = "unitno";
	unitOrder: boolean = false;
	isPenaltySetupDataLoaded: boolean = false;
	penaltyTable: FormGroup;
	touchedPenaltyRows: any;

	penaltyData: any = [];
	totalItems: any;
	itemLimit: any = 10;
	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	penaltyType: any;
	row: any = {};
	modalService: any;
	isAssetLoaded: boolean;
	isEditRow: boolean;
	wareHouseIndex: any;
	vatPecentageDataList: any;

	updatePenaltyRowIndex: any;
	glAccountList: any;
	blockDataList: any;
	deleteType: boolean = false;

	//penaltydatalistTable
	penaltyTypeDataList: any = [
		{
			penaltyTypedata: "1. % Interest per day",
			description: "Calculate the penalty after grace period. (Bill date of next month - Due date (grace period)"
		},
		{
			penaltyTypedata: "2. % Interest per month",
			description: "Even if there is one day delay in payment grace period date, per month interest rate will be charged"
		},
		{
			penaltyTypedata: "3. Flat Penalty per day",
			description: ""
		},
		{
			penaltyTypedata: "4. Flat Penalty per month",
			description: ""
		},
	];

	isActiveList: any = [
		{
			id: 1,
			isactive: "yes"
		},
		{
			id: 2,
			isactive: "no"
		}
	];



	constructor(private accountsService: AccountsService,
		private lookupService: LookupService,
		private sharedService: SharedService,
		private cookieService: CookieService,
		private injector: Injector,
		private apartmentService: ApartmentService,
		private fb: FormBuilder) {
		this.modalService = this.injector.get(ModalService);
	}


	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}
	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}
	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

	addPenaltyConfig(data) {

		let reqObj: any = {};
		reqObj = {
			"penaltyId": 0,
			"penaltyAmount": data.penaltyAmount,
			"isActive": true,
			"effectiveDate": data.effectiveDate,
			"isFlatfee": true,
			"interest": data.interest,
			"description": "string",
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"apartmentBlockId": data.apartmentBlockId,
			"penaltyTypeFrequencyConfigId": data.penaltyTypeFrequencyConfigId
		}
		let params: any = {};
		params.penaltyConfig = reqObj;
		console.log("added data ", params);
		this.accountsService.addPenaltyConfig(params).subscribe((res: any) => {
			if (res.message) {
				this.sharedService.setAlertMessage("Penalty Setup Config added successfully");
				this.isAssetLoaded = false;
				this.row = {};
				this.getAllPenaltyConfigByApartmentId();
			}
			else {
				this.sharedService.setAlertMessage(res.errorMessage);
			}

		});
	}

	updatePenaltyConfig(data) {
		let reqObj: any = {};
		reqObj = {
			"penaltyId": data.penaltyId,
			"penaltyAmount": data.penaltyAmount,
			"isActive": data.isActive,
			"effectiveDate": data.effectiveDate,
			"isFlatfee": data.isFlatfee,
			"interest": data.interest,
			"description": "string",
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"apartmentBlockId": data.apartmentBlockId,
			"penaltyTypeFrequencyConfigId": data.penaltyTypeFrequencyConfigId
		}
		let params: any = {};
		params.penaltyConfig = reqObj;
		this.accountsService.updatePenaltyConfig(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Penalty setup config Updated successfully");
				// this.isAssetLoaded = false;
				this.isEditRow = false;
				this.updatePenaltyRowIndex = -1;
				this.getAllPenaltyConfigByApartmentId();
			}

		});
	}

	deleteRow(index: number, data) {
		// const control = this.userTable.get('tableRows') as FormArray;
		// control.removeAt(index);
		console.log(data.penaltyId);
		this.deleteType = true;
		this.modalService.showConfirmModal(data.penaltyId);
	}

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}


	getAllPenaltyConfigByApartmentId() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: parseInt(this.cookieService.get('apartmentId')),
		};
		this.isAssetLoaded = true;
		this.accountsService.getAllPenaltyConfigByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				console.log("resp of penalty", res);
				// this.penaltyData = [];
				this.penaltyData = res && res.length > 0 ? res : [];
				this.isAssetLoaded = false;
				// this.tempWarehouseData = this.warehouseData;
				this.totalItems = this.penaltyData.length;


				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}
				else {
					this.ItemEndIndex = this.totalItems;
				}

			}
		});
	}

	getPenaltyConfigByPenaltyId() {
		let queryParamBase: any = {};
		console.log("data[0]", this.penaltyData);
		queryParamBase = {
			PenaltyId: 1
		};
		this.isAssetLoaded = true;
		this.accountsService.getPenaltyConfigByPenaltyId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.penaltyType = res ? res : [];
				console.log("penalty type ==> ", this.penaltyType)
			}
		});
	}

	getGlAccountsIdbyGlAccountTypeId() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			GLAccountTypeId: 165
		};
		//  this.isAssetLoaded = true;
		this.accountsService.getGlAccountsIdbyGlAccountTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
				// this.isAssetLoaded = false;
				this.glAccountList = res ? res : [];
				console.log(this.glAccountList);
			}
		});

	}

	getApartmentBlockByApartmentId() {

		let blockListParams = {
			apartmentId: parseInt(this.cookieService.get('apartmentId'))
		}

		this.apartmentService.getApartmentBlockByApartmentId(blockListParams).subscribe((res: any) => {
			this.blockDataList = res ? res : [];
		});

	}


	ngOnInit(): void {

		this.isPenaltySetupDataLoaded = true;
		// this.addPenaltyRow();

		this.getGlAccountsIdbyGlAccountTypeId();
		this.getPenaltyConfigByPenaltyId();
		this.getAllPenaltyConfigByApartmentId();
		this.getApartmentBlockByApartmentId();

		this.sharedService.unitlistdeleteindexcast.subscribe(id => {

			if (id != null) {
				if (this.deleteType) {
					let param: any = {};
					param = {
						apartmentId: this.cookieService.get('apartmentId'),
						PenaltyId: id,
						deleteBy: parseInt(this.cookieService.get('userId'))
					}
					this.accountsService.deletePenaltyConfig(param).subscribe((res: any) => {

						if (res.message) {
							this.getAllPenaltyConfigByApartmentId();
							setTimeout(() => {
								this.sharedService.setAlertMessage("Penalty deleted successfully");
								this.sharedService.setUnitListDeleteIndex(null);
							}, 500)
						}
						else {

							this.sharedService.setAlertMessage(res.errorMessage);
						}
						this.deleteType = false;
					}, error => {
						console.log(error);
					});
				}

			}

		})


	}

}
