import { Component, OnInit, ViewChild,TemplateRef, HostListener } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { jqxWindowComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxwindow';
import { jqxPopoverComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxpopover';
import { ConstantsService } from 'src/app/shared/services/constants.service';

@Component({
  selector: 'app-income-view-receipts',
  templateUrl: './income-view-receipts.component.html',
  styleUrls: ['./income-view-receipts.component.scss']
})
export class IncomeViewReceiptsComponent implements OnInit {
  	
  isReceiptsLoaded:boolean = false;

  receiptsData:string = "";
  receiptsDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "receiptNumber";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  selectedInput:string = "";
  columnField: any = {};

  receiptInfoDetails : any;

  isReceiptPrintLoaded:boolean = false;

  //view receipts info

  @ViewChild("viewReceiptsDetailsRef", { static: false }) viewReceiptsDetailsRef: TemplateRef<any>;

  columnData:any ;
  gridSourceData:any;
  popperDataSource:any
  reversePopOverSelectorReceipt:any;
  public submitFlag:boolean =true
  popoverSettings: jqwidgets.PopoverOptions;
  
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @ViewChild('reversePopWindow', { static: false }) reversePopWindow: jqxWindowComponent;
  @ViewChild('reversePopOver', { static: false }) reversePopOver: jqxPopoverComponent;
  @ViewChild("viewInvoiceDetailsRef", { static: false }) viewInvoiceDetailsRef: TemplateRef<any>;
  receipts: any;

  constructor(
    public dialog: MatDialog,
  	private accountsService:AccountsService,
    private cookieService:CookieService,
    private constantsService:ConstantsService,
  ) { }


  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  getEmailStatus(value){
  	if(value){
  		return 'Y'
  	}
  	else {
  		return 'N'
  	}
  }

  getDate(receipt, date){

   this.receiptsDataList.map(obj=> {
      if(obj.receiptNumber == receipt.receiptNumber){
        obj.paymentDateFormat = moment(date).format("DD/MM/YYYY");
      }
   });

  	return moment(date).format("DD/MM/YYYY"); 
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  setActive(item){
  	return ''
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  onGlSearchFilter() {

    if(this.receiptsData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.receiptsData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onEditReverse', ['$event.detail'])
  onEditReverse(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.receipts = dataRecord;
    this.reversePopOverSelectorReceipt = '#'+this.receipts.receiptNumber;    

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 100);

  }
  
  @HostListener('window:onUpdateInvoice', ['$event.detail'])
  onUpdateInvoice(detail) {

    var invoice = this.datagrid.getrowdata(detail.rowId);
   // this.updateInvoiceItem(invoice, detail.column);

  }
  //receiptInfo

  downloadReceiptEvent(){

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      collectionId: 188
    }
    this.isReceiptPrintLoaded = false;

    this.accountsService.getCollectionByCollectionId(params).subscribe((res:any) => {
      // this.custTaxDetails = res[0].custinvoiceTax
      this.isReceiptPrintLoaded = true
      this.receiptInfoDetails = res;
      console.log("receipt list ", res)
    })

    this.dialog.open(this.viewReceiptsDetailsRef, {
      panelClass: 'material',
      data: this.receiptInfoDetails
    });
  }

  getReceiptInfoDate(date){
    return moment(date).format(this.constantsService.dateFormat)
  }

  onClose(event){
    this.submitFlag=true
    this.tablebinding()
  }

  tablebinding(){
    
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.accountsService.getIncomeTrackerReceiptsByApartmentId(params)
    .subscribe((res:any) => {

  		this.receiptsDataList = res;
      this.receipts=this.receiptsDataList[0]
      this.reversePopOverSelectorReceipt='#'+this.receipts.receiptNumber
      this.gridSourceData = {
        localdata: 	this.receiptsDataList ,
        datatype: "array"
      }
      this.receiptsDataList  = new jqx.dataAdapter(this.gridSourceData);
  		this.totalItems = this.receiptsDataList.length;
      console.log('aaa',	this.receiptsDataList )
  		if(this.totalItems>this.itemLimit){
			  this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

		  this.isReceiptsLoaded = true;

  	}, error => {
    	console.log(error);
    });
  }
  
  ngOnInit() {
    this.popperDataSource={
      localdata:true,
      datatype:"boolean"
    }
    this.submitFlag=new jqx.dataAdapter(this.popperDataSource)
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: 'Receipt No',
      datafield: 'receiptNumber',
      width: 120,
      pinned: true,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell"><a href="javascript:void(0)" class="mr-2 t-decor" onClick="downloadInvoiceEvent('+ row +')">'+ value +'</a></div>'
      },
      renderer: columnrenderer
    },
    {
        text: 'Name',
        datafield: 'name',
        minwidth: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
    {
        text: 'Payment Date',
        datafield: 'paymentDate',
        minwidth: 120,        
        cellsrenderer: (row, column, value): string => {
          return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
        },
        renderer: columnrenderer
    },
    {
        text: 'Tower Unit',
        datafield: 'block_Unit',
        minwidth: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
    {
        text: 'Credit Note Amount',
        datafield: 'amount',
        minwidth: 150,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
    {
        text: 'Sent Email',
        datafield: 'isEmailSent',
        minwidth: 120,
        cellsrenderer: (row: number, column: string, value: boolean): string => {

        var chkedProperty;
        if(value){
          chkedProperty = 'checked="true"';
        }
        else {
          chkedProperty = '';
        }

          return '<div class="jqx-custom-inner-cell">'
                    + '<label class="switch float-left">'
                      + '<input type="checkbox" id="invoiceEmail" name="invoiceEmail"'+ chkedProperty +'>'
                      + '<div class="slider round" onClick="updateInvoiceEvent('+ row + ', \'email\')"></div>'
                    + '</label>'
              + '</div>'
        },
        renderer: columnrenderer
    },

    {
        text: 'Actions',
        cellsalign: 'center', 
        align: 'center',
        minwidth: 120,
        datafield: 'isReversed',
        cellsrenderer: (row,coloumn,value): any => {
        let isreverse=value
        let reverse ={'obj1':'d-block','obj2':'d-none'}
        let rdata={'obj1':'d-none','obj2':'d-block'}         
        let valueData=isreverse===false ? reverse  : rdata
        let elemId = this.receiptsDataList.loadedData[row].receiptNumber;
         // return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editInvoiceEvent('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" id="'+ elemId +'" onClick="editReverseEvent('+ row +')"><i class="fa fa-repeat icon delete" aria-hidden="true"></i></a></div>'
          return '<div class="simple-actions">'
                
                    +'<div class="'+ valueData.obj1 +'">'
                        +'<div href="javascript:void(0)" class="mr-2" id="'+ elemId +'" onClick="editReverseEvent('+ row +')">'
                          +'<i class="fa fa-repeat icon delete" aria-hidden="true"></i>'
                        +'</div>'
                    +'</div>'
                    +'<div>'
                      +'<span class="'+ valueData.obj2 +'">R</span>'
                    +'</div>'
                 +'</div>'
        },
        renderer: columnrenderer
    }];
    this.tablebinding()

  }
  
}


function editReverseEvent(row){
  
  var event = new CustomEvent('onEditReverse', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editReverseEvent = editReverseEvent;

function updateInvoiceEvent(row, type){

  var event = new CustomEvent('onUpdateInvoice', {
    detail: {
        rowId: row,
        column: type
    }
  })
  window.dispatchEvent(event);
}
(window as any).updateInvoiceEvent = updateInvoiceEvent;