import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
@Component({
  selector: 'app-income-history-reverse',
  templateUrl: './income-history-reverse.component.html',
  styleUrls: ['./income-history-reverse.component.scss']
})
export class IncomeHistoryReverseComponent implements OnInit {
  isReceiptSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "" ;

  @Input() invoice:any;
  @Output () public childEvent:any=new EventEmitter()
  constructor(
    private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { }

  submitReverseIncomeHistoryForm(form){

  	this.isReceiptSubmitted = false;

	  let details = 
{
    "apartmentId": parseInt(this.cookieService.get('apartmentId')),
    "amount": this.invoice.invoiceAmount,
    "collectionId": 0,
    "invoiceId": this.invoice.billNo,
    "isEmailSent": true,
    "isSmssent": true,
    "comment": this.invoice.comment,
    "isActive": true,
    "insertedBy": parseInt(this.cookieService.get('userId')),
    "insertedOn": new Date().toISOString(),
    "updatedBy": 0,
    "updatedOn": new Date().toISOString(),
    "transReference1": "string",
    "transReference2": "string",
    "glAcctIndicator": "string"

  } 
  let params = {
    custTransReversal: details
  }

  this.accountsService.addCustTransReversal(params).subscribe((res:any) => {

      if(res.message){

  			this.isReceiptSubmitted = true;
  			this.sharedService.setAlertMessage("Invoice reversed successfully");

      }
      else {

  			this.isReceiptSubmitted = true;
  			this.isError = true;
  			this.alertMessage = res.errorMessage;

      }

      },
    error => {
      this.isReceiptSubmitted = true;
      this.isError = true;
      this.alertMessage = "Some error occured";
	},
	()=>{//this.isReceiptSubmitted=true
		this.childEvent.emit(true)
	});



  }
  ngOnInit(): void {
  }

}
