import { Component, OnInit, Injector } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

@Component({
	selector: 'app-income-tracker-setup-auto-invoicing',
	templateUrl: './income-tracker-setup-auto-invoicing.component.html',
	styleUrls: ['./income-tracker-setup-auto-invoicing.component.scss']
})
export class IncomeTrackerSetupAutoInvoicingComponent implements OnInit {

	isAutoInvoicingDataLoaded: boolean = false;
	unitFieldType: string = "unitno";
	unitOrder: boolean = false;
	touchedRows: any;
	autoInvoicingTable: FormGroup;
	glAccountList: any;

	autoInvoicingData: any = [];
	ItemStartIndex: any = 0;
	ItemEndIndex: any;
	totalItems: any;
	itemLimit: any = 10;
	row: any = {};
	isEditRow: boolean;
	updateAutoInvoiceIndex: any;
	vatPecentageDataList: any;
	taxPecentageDataList: any;
	modalService: any;
	blockDataList: any;
	deleteType:  boolean = false;


	constructor(private accountsService: AccountsService,
		private lookupService: LookupService,
		private sharedService: SharedService,
		private cookieService: CookieService,
		private fb: FormBuilder,
		private apartmentService:ApartmentService,
		private injector: Injector) {
		this.modalService = this.injector.get(ModalService);

	}


	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}
	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}
	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

	getIndexParams(event) {
		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	}

	addAutoinvoiceConfig(data) {

		let reqObj: any = {};
		reqObj = {
			"id": data.id,
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"tower":  data.tower,
			"category": "string",
			"glaccountId": data.glaccountId,
			"feeperSqftpermonth": data.feeperSqftpermonth,
			"fixedFee": data.fixedFee,
			"vatId": data.vatId,
			"taxId": data.taxId,
			"active": true,
			"effectiveDate": data.effectiveDate,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.autoinvoiceConfig = reqObj;
		this.accountsService.addAutoinvoiceConfig(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Auto invoice config added successfully");
				this.row = {};
				this.getAutoinvoiceConfigByApartmentId();
			}
		});
		
	}

	updateAutoinvoiceConfig(data) {
		let reqObj: any = {};
		reqObj = {
			"id": data.id,
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"tower": data.tower,
			"category": "string",
			"glaccountId": data.glaccountId,
			"feeperSqftpermonth": parseInt(data.feeperSqftpermonth),
			"fixedFee": parseInt(data.fixedFee),
			"vatId": parseInt(data.vatId),
			"taxId": parseInt(data.taxId),
			"active": true,
			"effectiveDate": data.effectiveDate,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString,
			"updatedBy": parseInt(this.cookieService.get('userId')),
			"updatedOn": new Date().toISOString,
		}
		let params: any = {};
		params.autoinvoiceConfig = reqObj;
		this.accountsService.updateAutoinvoiceConfig(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Auto invoice config Updated successfully");
				this.isEditRow = false;
				this.updateAutoInvoiceIndex = -1;
				this.row = {};
				this.getAutoinvoiceConfigByApartmentId();

			}

		});
	}

	getAutoinvoiceConfigByApartmentId() {

		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
		};
		this.accountsService.getAutoinvoiceConfigByApartmentId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.autoInvoicingData = res && res.length > 0 ? res : [];
				console.log("auto invoice ", this.autoInvoicingData);
				this.totalItems = this.autoInvoicingData.length;


				if (this.totalItems > this.itemLimit) {
					this.ItemEndIndex = this.itemLimit;
				}
				else {
					this.ItemEndIndex = this.totalItems;
				}
			}
		});
	}

	getGlAccountsIdbyGlAccountTypeId() {
		let queryParamBase: any = {};
		queryParamBase = {
			apartmentId: this.cookieService.get('apartmentId'),
			GLAccountTypeId: 165
		};
		this.accountsService.getGlAccountsIdbyGlAccountTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.glAccountList = res ? res : [];
				console.log(this.glAccountList);
			}
		});

	}

	getApartmentBlockByApartmentId() {

		let blockListParams = {
			apartmentId: parseInt(this.cookieService.get('apartmentId'))
		}

		this.apartmentService.getApartmentBlockByApartmentId(blockListParams).subscribe((res: any) => {
			this.blockDataList = res ? res : [];
		});

	}

	deleteRow(index: number, data) {
		this.deleteType = true; 
		console.log("data.id", data);
		this.modalService.showConfirmModal(data.id);
	}

	ngOnInit(): void {

		this.isAutoInvoicingDataLoaded = true;
		this.getApartmentBlockByApartmentId();

		//VAT

		let vatListParams = {
			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
			LookupTypeId: 77
		}
		this.lookupService.getLookupValueByLookupTypeId(vatListParams).subscribe((res: any) => {

			this.vatPecentageDataList = res.filter(item => {
				return item.isActive;
			});

		});

		//TAx

		let taxListParams = {
			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
			LookupTypeId: 183
		}
		this.lookupService.getLookupValueByLookupTypeId(taxListParams).subscribe((res: any) => {

			this.taxPecentageDataList = res.filter(item => {
				return item.isActive;
			});

		});

		this.getGlAccountsIdbyGlAccountTypeId();
		setTimeout(() => {
			this.getAutoinvoiceConfigByApartmentId();

		}, 1000);

		//DeleteRow

		this.sharedService.unitlistdeleteindexcast.subscribe(id => {

			console.log("id", id);

			if (id != null) {

				if(this.deleteType) {
					let deleteparam: any = {};
					deleteparam = {
						apartmentId: this.cookieService.get('apartmentId'),
						Id: id,
						deleteBy: parseInt(this.cookieService.get('userId'))
					}
					this.accountsService.deleteAutoinvoiceConfig(deleteparam).subscribe((res: any) => {
						
						if (res.message) {
							this.getAutoinvoiceConfigByApartmentId();
							setTimeout(() => {
								this.sharedService.setAlertMessage("Auto invoicing deleted successfully");
								this.sharedService.setUnitListDeleteIndex(null);

							
							}, 500)
						}
						else {
							this.sharedService.setAlertMessage(res.errorMessage);
						}
						this.deleteType = true;
					}, error => {
						console.log(error);
					});

				}
			}
		});
	}
}



