import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { UserService } from 'src/app/api/controllers/User';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { jqxWindowComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxwindow';
import { jqxPopoverComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxpopover';
import { ConstantsService } from '../../../../shared/services/constants.service';

@Component({
  selector: 'app-income-view-invoice-history',
  templateUrl: './income-view-invoice-history.component.html',
  styleUrls: ['./income-view-invoice-history.component.scss']
})
export class IncomeViewInvoiceHistoryComponent implements OnInit {
  
  isInvoiceDataLoaded:boolean = false;
  isInvoiceDataTableLoaded:boolean = true;

  invoiceData:string = "";
  invoiceDataList:any;

  accountDataList:any;
  isAccountDataLoaded:boolean = false;

  userDataList:any;
  glAccountListData:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  accountUserDetails:any;

  reverse:any;

  filterObject:any;

  selectedInput:string = "";
  columnField: any = {}

  isReverseSubmitted:boolean = true;

  columnData:any ;
  gridSourceData:any;
  invoicePopOverSlector:any;
  public submitFlag:boolean =true
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @ViewChild('reversePopOver', { static: false }) reversePopOver: jqxPopoverComponent;
  invoice: any;

  popperDataSource: { localdata: boolean; datatype: string; };

  constructor(
    private route: ActivatedRoute,
    private userService:UserService,
  	private accountsService:AccountsService,
  	private sharedService:SharedService,
    private cookieService:CookieService,
    private constantsService:ConstantsService,
    private router:Router
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("MM-DD-YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  getUserName(id){

  	var data = _.filter( this.userDataList, function( item ) {
      if(item.userId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].firstName+' '+data[0].lastName;
    }

  }

  getInvoiceDate(itr, date){

    _.each( this.invoiceDataList, (obj, index) => {
        obj.postedOn = moment(obj.custInvoiceDate).format("MM-DD-YYYY");
    });

  	return moment(date).format("MM-DD-YYYY");
  }

  getAccountName(account){

    var accountDetails = {
      glaccountId: 0
    };

    if(account == undefined || account.length == 0){
      accountDetails.glaccountId = 0;
    }
    else {
      accountDetails.glaccountId = account[0].glaccountId;
    }

  	var data = _.filter( this.glAccountListData, function( item ) {
      if(item.glaccountId === accountDetails.glaccountId)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].glaccountName;
    }

  	return '';
  }

  getDueAmount(due, paid){
    return (due-paid) > 0 ? 'high' : 'low';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  getFieldParams(event){

    this.isInvoiceDataTableLoaded = false;

    var postStartDate = moment(event.PostStartDate).format('MM-DD-YYYY');
    var postEndDate = moment(event.PostEndDate).format('MM-DD-YYYY');

    var params = {
      ApartmentBlockUnitID: this.route.params['value'].id,
      PostStartDate: postStartDate,
      PostEndDate: postEndDate,
    }

    this.accountsService.getAccountHistoryByApartmentUnitId(params).subscribe((res:any) => {

      this.invoiceDataList = res;

      //filter items
      if(event.isReversed != null){
        this.invoiceDataList = this.invoiceDataList.filter(item => { return item.isReversed == event.isReversed  });
      }

      if(event.isCreditNote != null){
        this.invoiceDataList = this.invoiceDataList.filter(item => { return item.isCreditNote == event.isCreditNote  });
      }
      
      if(event.postedDate != null){
        this.invoiceDataList = this.invoiceDataList.filter(item => { return moment(item.postedDate).format("MM-DD-YYYY") == moment(event.postedDate).format("MM-DD-YYYY")  });
      }

      if(event.billNo != null){
        this.invoiceDataList = this.invoiceDataList.filter(item => { return item.billNo == event.billNo  });
      }

      if(event.receiptNo != null){
        this.invoiceDataList = this.invoiceDataList.filter(item => { return item.receiptNo == event.receiptNo  });
      }

      this.invoiceDataList.sort((a, b) => {
        return new Date(b.postedDate).getTime() - new Date(a.postedDate).getTime()
      });


      this.totalItems = this.invoiceDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isInvoiceDataTableLoaded = true;


    }, error => {
      
    });

  }

  reverseItem(item){
      
      this.isReverseSubmitted = false;

        let details = {

          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "amount": item.invoiceAmount,
          "collectionId": 0,
          "invoiceId": item.billNo,
          "isEmailSent": true,
          "isSmssent": true,
          "comment": this.reverse.comment,
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": 0,
          "updatedOn": new Date().toISOString(),
          "transReference1": "string",
          "transReference2": "string",
          "glAcctIndicator": "string"

        }

        let params = {
          custTransReversal: details
        }

        this.accountsService.addCustTransReversal(params).subscribe((res:any) => {

          if(res.message){

            this.reverse.comment = "";
            this.sharedService.setAlertMessage("Invoice Reversed");

            var params = {
              ApartmentBlockUnitID: this.route.params['value'].id
            }

            this.accountsService.getAccountHistoryByApartmentUnitId(params).subscribe((res:any) => {


              this.invoiceDataList = res;

              this.invoiceDataList.sort((a, b) => {
                return new Date(b.postedDate).getTime() - new Date(a.postedDate).getTime()
              });

              this.totalItems = this.invoiceDataList.length;

              if(this.totalItems>this.itemLimit){
                this.ItemEndIndex = this.itemLimit;
              }
              else {
                this.ItemEndIndex = this.totalItems;
              }

              this.isReverseSubmitted = true;

            });

          }
          else {



          }

        }, error => {
        
        });


  }
  onClose(event){
    this.submitFlag=event
    this.tableBinding()
  }
  tableBinding(){
   
    this.accountsService.getAllGlAccounts().subscribe((res:any) => {

      var glAccountListData = res.filter(item => {
        return item.apartmentId == parseInt(this.cookieService.get('apartmentId'));
      });

      this.glAccountListData = glAccountListData;

  });
    var params = {
      ApartmentBlockUnitID: this.route.params['value'].id
    }

    
    this.accountsService.getAccountHistoryByApartmentUnitId(params).subscribe((res:any) => {

      this.invoiceDataList = res;
      this.invoice=this.invoiceDataList[0]
      this.invoicePopOverSlector='#'+this.invoice.billNo
      //sorting by date ( new to old )
      this.invoiceDataList.sort((a, b) => {
        return new Date(b.postedDate).getTime() - new Date(a.postedDate).getTime()
      });

  		this.totalItems = this.invoiceDataList.length;

      this.gridSourceData = {
        localdata: 	this.invoiceDataList ,
        datatype: "array"
      }

      this.invoiceDataList=  new jqx.dataAdapter(this.gridSourceData);
  		if(this.totalItems>this.itemLimit){
			  this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
		  }

      let accountListParams = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }

      this.accountsService.getIncomeTrackerSubLedgersByApartmentId(accountListParams).subscribe((res:any) => {

        this.accountDataList = res.filter(item => {
          return item.apartmentBlockUnitId == this.route.params['value'].id;
        });

        this.isAccountDataLoaded = true;

      });

      let userListParams = {
        apartmentId: parseInt(this.cookieService.get('apartmentId'))
      }

      this.userService.getAllUsersByApartmentId(userListParams).subscribe((res:any) => {

        this.userDataList = res;
        this.isInvoiceDataLoaded = true;

      }, error => {
        
      });


  		

    },
    ()=>{
      
      
    })

  }
  onGlSearchFilter() {

    if(this.invoiceData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.invoiceData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }
  
  @HostListener('window:onAccHistroyEdit', ['$event.detail'])
  onAccHistroyEdit(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let billNo = dataRecord.billNo
    this.router.navigateByUrl('/ams/income/edit-invoice/' + billNo);
  }

  
  @HostListener('window:onEditReverse', ['$event.detail'])
  onEditReverse(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    this.invoice = dataRecord;
    this.invoicePopOverSlector = '#'+this.invoice.billNo;    

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 100);

  }
  
  ngOnInit() {
    this.popperDataSource={
      localdata:true,
      datatype:"boolean"
    }
    this.submitFlag=new jqx.dataAdapter(this.popperDataSource)
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: 'Posted Date',
      datafield: 'postedDate',
      width: 120,
//      pinned: true,
      cellsrenderer: (row, column, value): string => {
        return '<div class="jqx-custom-inner-cell">' + moment(value).format(this.constantsService.dateFormat) + '</div>'
      },
      renderer: columnrenderer
    },
    {
        text: 'Bill No',
        datafield: 'billNo',
        minwidth: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
    {
        text: 'Reversal Id',
        datafield: 'reversalId',
        minwidth: 120,        
        renderer: columnrenderer
    },
    {
        text: 'Receipt No',
        datafield: 'receiptNo',
        minwidth: 120,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
    {
        text: 'Billed Amount',
        datafield: 'invoiceAmount',
        minwidth: 150,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },
    {
        text: 'Reciept Amount',
        datafield: 'amountPaid',
        minwidth: 120,        
        renderer: columnrenderer
    },
    {
      text: 'Due',
      datafield: 'balance',
      minwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
  },
//   {
//     text: 'Status',
//     datafield: 'postedBy',
//     minwidth: 150,
//     cellsrenderer: cellsrenderer,
//     renderer: columnrenderer
// },
    {
        text: 'Actions',
        cellsalign: 'center', 
        align: 'center',
        minwidth: 120,
        datafield: 'isReversed',
        cellsrenderer: (row,coloumn,value): any => {
        let isreverse=value
        let reverse ={'obj1':'d-block','obj2':'d-none'}
        let rdata={'obj1':'d-none','obj2':'d-block'}         
        let valueData=isreverse===false ? reverse  : rdata
        let elemId = this.invoiceDataList.loadedData[row].billNo
         // return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editInvoiceEvent('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" id="'+ elemId +'" onClick="editReverseEvent('+ row +')"><i class="fa fa-repeat icon delete" aria-hidden="true"></i></a></div>'
          return '<div class="simple-actions">'
                
                    +'<div class="'+ valueData.obj1 +'">'
                        +'<div href="javascript:void(0)" class="mr-2" id="'+ elemId +'" onClick="editReverseEvent('+ row +')">'
                          +'<i class="fa fa-repeat icon delete" aria-hidden="true"></i>'
                        +'</div>'
                    +'</div>'
                    +'<div>'
                      +'<span class="'+ valueData.obj2 +'">R</span>'
                    +'</div>'
                    +'<a href="javascript:void(0)" class="mr-2" onClick="editAccHistroyEvent(' + row + ')">'
                      +'<i class="fa fa-pencil icon edit" aria-hidden="true"></i>'
                    +'</a>'
                 +'</div>'
        },
        renderer: columnrenderer
    }];
    this.reverse = {};


this.tableBinding()

  }

}

function editReverseEvent(row){
  
  var event = new CustomEvent('onEditReverse', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editReverseEvent = editReverseEvent;

function editAccHistroyEvent(row) {
  var event = new CustomEvent('onAccHistroyEdit', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editAccHistroyEvent = editAccHistroyEvent;
